  background: #ad2320; }

.navigation__item:active {
  background: #8a1c19; }

.navigation__item:hover .navigation__link,
.navigation__item:hover .navigation__link {
  color: #ffffff; }

.navigation__item:hover .navigation__icon rect,
.navigation__item:focus .navigation__icon rect {
  fill: #ffffff; }

.navigation__item--active {
  background: #ad2320; }

.navigation__item--active .navigation__link {
  color: #ffffff; }

.navigation__item--active .navigation__icon rect {
  fill: #ffffff; }

.navigation__item--toggle:hover .navigation__icon,
.navigation__item--toggle:focus .navigation__icon {
  transform: rotate(180deg); }

.navigation__link {
  text-decoration: none;
