  border-right: 1rem solid #e2e2e2; }

.navigation__list--inner .navigation__item .navigation__link,
.navigation__list--inner .navigation__item .navigation__link {
  color: #353533; }

.navigation__list--inner .navigation__item:first-child {
  border-top: 1rem solid #e2e2e2; }

.navigation__list--inner .navigation__item {
  border-bottom: 1rem solid #e2e2e2;
  width: 100%;
