.navigation__list--inner .navigation__item:hover,
.navigation__list--inner .navigation__item:focus {
  background-color: #e2e2e2; }

.navigation__list--inner .navigation__item:active {
  background-color: #d9d9d9; }

.navigation__list--inner .navigation__item:hover .navigation__link,
.navigation__list--inner .navigation__item:focus .navigation__link {
  color: #353533; }

.navigation__list--inner .navigation__link {
  width: 100%;
