.navigation__item--toggle:hover .navigation__list--inner {
  display: block; }

.navigation__item--toggle:hover,
.navigation__item--toggle:focus {
  background: #ad2320; }

.navigation__item--toggle:active {
  background: #ad2320; }

.navigation__item--toggle:hover .navigation__list--inner,
.navigation__item--toggle:focus .navigation__list--inner {
  display: flex; }

@media (max-width: 767px) {
  .navigation__list--inner {
    width: 100%; }
  .navigation__link {
    font-size: 14rem;
    line-height: 24rem;
    padding: 12rem 17rem; }
  .navigation__list--inner {
    position: unset; }
  .navigation__list--inner .navigation__item {
    background: #f2f2f2; }
  .navigation .navigation__item--toggle-active .navigation__icon {
    transform: rotate(0); }
  .navigation__link--toggle .navigation__icon {
    width: 12rem;
    height: 8rem; } }

