.phone {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 302rem;
  min-height: 80rem;
  background: #F2F2F2;
  margin-left: 96rem;
}

.phone__icon {
  width: 18rem;
  height: 18rem;
  margin-right: 12rem;
}

.phone__link {
  font-family: $font-default;
  font-weight: 500;
  font-size: 24rem;
  line-height: 23rem;
  color: $text-default;
  text-decoration: none;
}

@media (max-width: $width-tablet - 1) {
  .phone {
    background: none;
    margin: 0;
    width: 30rem;
    min-height: unset;
    height: 30rem;
    display: flex;
  }

  .phone__icon {
    width: 30rem;
    height: 30rem;
    margin: 0;
  }

  .phone__link {
    display: none;
  }
}
